<template>
  <div>
      <!-- 无子集 -->
      <el-menu-item :index="path" v-if="!item.child">
        
        <span slot="title">{{item.name}}</span>
      </el-menu-item>

      <!-- 有子集 -->
      <el-submenu :index="path" v-else>
      <template slot="title">
        <i class="el-icon-menu"></i>
        <span>{{item.name}}</span>
      </template>
        <NavItem v-for="child in item.child" :key="child.url" :item="child" :path="child.url" />
      </el-submenu>

  </div>
</template>

<script>
export default {
  name:'NavItem',
  data() {
    return{
      items:[]
    }
  },
  props:['item','path']
}
</script>

